:root {
  --primary: #bbdefb;
  --secondary: #004bb8;
  --dark: #455a64;
  --light: #fafafa;
}

.card {
  margin: 100px auto 0;
  width: 250px;
  height: 370px;
  -webkit-perspective: 1000px;
          perspective: 1000px;

}

.card__inner {
  width: 100%;
  height: 100%;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
}

.card__inner.is-flipped{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  border-radius: 16px;
  box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
}

.card__face--front {
  background-image: linear-gradient(
    to bottom right,
    #bbdefb,
    #004bb8);
  background-image: linear-gradient(
    to bottom right,
    var(--primary),
    var(--secondary)
  );
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.card__face--front Typography {
  color: #fafafa;
  font-size: 32px;
}

.card__face--back {
  background-color: #fafafa;
  background-color: var(--light);
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card__content {
  width: 100%;
  height: 100%;
}

.card__header {
  position: relative;
  padding: 30px 30px 40px;
}

.card__header::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  border-radius:0 0 50% 50%;
  background-image: linear-gradient(
    to bottom left,
    #bbdefb 10%,
    #004bb8 115%
  );
  background-image: linear-gradient(
    to bottom left,
    var(--primary) 10%,
    var(--secondary) 115%
  );
  }

  .imgFront{
      display:block;
      width: 90px;
      height: 90px;
      margin:  0 auto 30px;
      border-radius: 50%;
      background-color:#fafafa;
      background-color:var(--light);
      border: 5px solid #fafafa;
      object-fit:cover ;
  }

  .imgHeader{
    display:block;
    width: 75px;
    height: 75px;
    margin:  0 auto 30px;
    border-radius: 50%;
    background-color:#fafafa;
    background-color:var(--light);
    border: 5px solid #fafafa;
    object-fit:cover ;
  
}

